import React, {Component} from "react";
import TableRow from "./TableRow";
import $ from "jquery";

export default class EmailButton extends Component {
  state = {
    loading: "",
  };

  sendDelegateEmail() {
    const name = this.props.name;
    const id = this.makeDelegateEmailIdentifier(name);
    const email = $('#'+id).val();
    if (!email || !EmailButton.isEmail(email)) {
      alert('The delegate email is invalid. Please try entering again.');
      return;
    }
    this.sendEmail(email);
  }

  static isEmail(email) {
    return email.match(/^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,}$/);
  }

  onClick() {
    this.sendEmail(this.props.email);
  }

  sendEmail(email) {
    const dateOfReport = this.props.dateOfReport;
    const link = this.props.link;
    const name = this.props.name;
    const query = {
      action: 'sendVerificationEmail',
      email: email,
      name: name,
      dateOfReport: dateOfReport,
    }
    this.setState({
      ...this.state,
      loading: "Sending..."
    });
    return new Promise((resolve, reject) => {
      const requestOptions = {
        method: 'post',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(query)
      };
      fetch(link, requestOptions)
      .then(response => response.json())
      .then(items => {
        console.log(items['mssg']);
        this.setState({
          ...this.state,
          loading: items['mssg'],
        });
      })
      .catch(error => {
        console.error(error);
        this.setState({
          ...this.state,
          loading: "",
        });
        let mssg = "General failure";
        if (error.toString().match(/Unexpected token/)) {
            mssg = 'Unable to access database to send email. (You might need to login to REDCap again.)';
        } else {
            mssg = error+' Unable to access database to send email.';
        }
        alert(mssg);
      });
    });
  }

  makeDelegateEmailIdentifier(name) {
    return TableRow.makeHTMLId(name)+"_delegateEmail";
  }

  checkShowDelegateButton() {
    const name = this.props.name;
    const delegateEmailIdentifier = this.makeDelegateEmailIdentifier(name);
    const delegateButtonIdentifier = delegateEmailIdentifier+"_button";
    const requestedEmail = $('#'+delegateEmailIdentifier).val();
    if (EmailButton.isEmail(requestedEmail)) {
      $('#'+delegateButtonIdentifier).show();
    } else {
      $('#'+delegateButtonIdentifier).hide();
    }
  }

  static makeMailto(email) {
    if (!email) {
      return "";
    }
    if (EmailButton.isEmail(email)) {
      const hrefClause = 'mailto:'+email;
      const smallEmail = 'smallEmail';
      return <a href={hrefClause} className={smallEmail}>{email}</a>;
    }
    return "";
  }

  render() {
    const name = this.props.name;
    const paragraphKey = name+"_paragraph";
    const loadingKey = name+"_loading";
    const buttonKey = name+"_button";
    const delegateParagraphKey = name+"_delegateParagraph";
    const delegateKey = name+"_delegate";
    const delegateButtonKey = name+"_delegateButton";
    const delegateEmailIdentifier = this.makeDelegateEmailIdentifier(name);
    const delegateButtonIdentifier = delegateEmailIdentifier+"_button";
    const paragraphClasses = ['nomargin'];
    const hideStyle = {display: 'none'};
    const delegateEmail = <p key={delegateParagraphKey} className={paragraphClasses}>
      Alternate Email: <input key={delegateKey} id={delegateEmailIdentifier} onChange={this.checkShowDelegateButton.bind(this)} /><br/>
      <button style={hideStyle} id={delegateButtonIdentifier} key={delegateButtonKey} onClick={this.sendDelegateEmail.bind(this)}>Send Email to Alternate</button>
    </p>;

    let loadingInfo = "";
    if (this.state.loading) {
      loadingInfo = <p key={loadingKey} className={paragraphClasses}>{this.state.loading}</p>;
    }
    if (this.props.email) {
      return (
        <section>
          {delegateEmail}
          <p key={paragraphKey} className={paragraphClasses}>
            <button key={buttonKey} onClick={this.onClick.bind(this)}>Send Email to Scholar</button>
          </p>
          {loadingInfo}
        </section>
      );
    } else {
      return (
        <section>
          {delegateEmail}
          <p key={paragraphKey} className={paragraphClasses}>
            No email provided.
          </p>
          {loadingInfo}
        </section>
      );
    }
  }
}

